import React,{useState, useEffect} from 'react';
import classes from './Orders.css';
import { connect } from 'react-redux';
import * as action from '../../store/action/index';

import Layout from '../../continar/Layout/Layout';

import Modal from '../UI/modal/modal';
import { Link } from 'react-router-dom';

const Orders=(props)=>{
    const [mdl, setmdl]= useState(false);
    const [dataid, setdataid]= useState()

    useEffect(()=>{
        
        props.ongetinitorder()
        
    },[])
    
    const mdlhandller=(id)=>{
        setdataid({
            lastName: props.order[id].lastName,
            fristName: props.order[id].fristName,
            address: props.order[id].address,
            phoneNumber: props.order[id].phoneNumber,
            order: props.order[id].order
        })
        setmdl(true)
    }
    const orderasthandler=()=>{
            
            
            props.onpostinitorder(dataid)
           
            
    }
    
    let orderList= <Link path='/'/>;
    if(props.order){
        const ormp= props.order.map(mi=>{
            return mi.order
        })

        
        orderList= ormp.map((mp, index)=>(
            <div className={classes.box} key={index}>
                {mp.map((mi,ind)=>(
                    <div className={classes.tipical}> 
                        <span className={classes.span} key={ind}>some : {mi.some} ,</span>
                        <span className={classes.span}>name : {mi.name} ,</span>
                        <span className={classes.span}>price : {mi.price}</span>
                    </div>
                    
                ))}
                  <button  className={classes.btn} onClick={()=>mdlhandller(index)}>ثبت سفارش دوباره</button>
            </div>
        ))
    }
    let modalorder= null;
    if(mdl){
        modalorder= <Modal clickmdl={orderasthandler} clicksub={()=>setmdl(false)}> آیا از ثبت دوباره سفارش خود مطمئن هستید؟</Modal>
    }
    return(
        <React.Fragment>
            <div className={classes.orders} style={{height:window.innerHeight}}>
            <Layout/>
            
                <div className={classes.target}>
                    <div className={classes.target_order}>
                        
                         {orderList}   
                        {modalorder}            
                    </div>
                </div>
                </div>
        </React.Fragment>
        
    );
};

const mapstatetoprops=state=>{
    return{
        order: state.order.order
    }
}
const mapdispatchtoprops=dispatch=>{
    return{
        ongetinitorder: ()=>dispatch(action.getinitorder()),
        onpostinitorder:(dt)=>dispatch(action.postinitorder(dt))
    }
}
export default connect(mapstatetoprops, mapdispatchtoprops)(Orders);